<template>
  <v-app>
    <v-content>
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <v-flex xs12 sm8 md4>
            <v-card class="elevation-12">
              <v-toolbar dark color="primary">
                <v-toolbar-title>Ororus商城后台管理</v-toolbar-title>
                <v-spacer></v-spacer>
              </v-toolbar>
              <v-card-text>
                <v-form>
                  <v-text-field prepend-icon="person" v-model="email" label="用户名" type="text"/>
                  <v-text-field
                    prepend-icon="lock"
                    v-model="password"
                    label="密码"
                    id="password"
                    :append-icon="e1 ? 'visibility' : 'visibility_off'"
                    :append-icon-cb="() => (e1 = !e1)"
                    :type="e1 ? 'text' : 'password'"
                 ></v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" @click="doLogin">登录</v-btn>
              </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
    <v-dialog v-model="dialog" width="300px">
      <v-alert icon="warning" color="error" :value="true">
      {{msg}}
      </v-alert>
    </v-dialog>
  </v-app>
</template>

<script>
  import {mapMutations} from 'vuex'
export default {

  data: () => ({
    email: "",
    password: "",
    dialog: false,
    e1:false,
    msg:""
  }),
  computed:{
  },
  methods: {
    ...mapMutations(['adduser']),
    doLogin() {
      if (!this.email || !this.password) {
        this.msg="用户名和密码不能为空";
        this.dialog = true;
        return false;
      }
      this.$http.post("/User/adminlogin",this.$qs.stringify({email:this.email,
        password:this.password}))
        .then((resp)=>{
          this.adduser(resp.data);
          this.$router.push('/index');
        })
        .catch((error)=>{
          this.msg="账号密码错误或者权限不足";
          this.dialog = true;
          console.log(error);
        })
      /*this.$router.push("/index");*/
    }
  }
};
</script>



